<template>
    <ve-map :data="chartData" :settings="chartSettings"></ve-map>
</template>

<script>
export default {
    name:'Map',
    data () {
        this.chartSettings = {
            position: 'world',
            label: false,
        }

        return {
            chartData: {
                columns: ['位置', '人口'],
                rows: [
                    { '位置': '吉林', ' 人口': 123 },
                    { '位置': '北京', ' 人口': 1223 },
                    { '位置': '上海', ' 人口': 2123 },
                    { '位置': '浙江', ' 人口': 4123 }
                ]
            }
        }
    }
}
</script>

<style scoped>

</style>



